.nav-item{
    width: 100%;
    height: 3%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 9rem;
    padding: 1.4rem 1.5rem;
    position: fixed;
    top: 0;
    background-color: rgba(160, 170, 179, 0.767);
}

.nav-item .links{
    text-transform: uppercase;
    color: rgb(0, 0, 0);
    font-weight: 600;
    padding: 20px;
    text-decoration: none;
    font-size: 1rem;
}
.nav-item .links:hover{
    transform: scale(2);
    text-decoration: underline;
}


