.login{
    background-color: rgba(140, 148, 150, 0.568);
    width: 100%;
    height: 100vh;
}
.parent{
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    width: 80%;
    height: 80vh;
    position: relative;
    top: 85px;
    margin: auto;
    background: rgba(255, 255, 255, 0.463);
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
    
}
.firstChild{
    width: 35%;
    padding: 10%;
}
.secondChild{
    width: 55%;
    height: 100%;
    border-left: 4px solid #aaa;
}
.secondChild img{
    width: 100%;
    height: 100%;
}

form input{
    display: block;
    width: 20rem;
    height: 2.3rem;
    margin-bottom: 1em;
    border: none;
    border-radius: 6px;
    outline: none;
}
input[type="submit"]{
    background-color: rgb(37, 37, 188);
    padding: 5px;
    color: #fff;
    font-size: 1.2em;
    transition: 1s;
    cursor: pointer;
}
input[type="submit"]:hover{
    color: rgb(37, 37, 188);
    background: #f2f2f2;
    border: 2px solid rgb(37, 37, 188);
}
.firstChild a{
    margin-left: 2rem;
}
.firstChild a img{
    width: auto;
    transition: 1.2s;
}
.firstChild a img:hover{
    transform: scale(1.2);
}