.home{
    width: 100%;
    height: 91.2vh;
    background: rgba(176, 183, 182, 0.788);
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.home .content{
    width: 40%;
}
.home .store{
    width: 40%;
    overflow: hidden;
}
.store img{
    width: 100%;
}
.content button,button{
    width: 14rem;
    height: 3rem;
    background-color: rgb(48, 48, 230);
    color: aliceblue;
    border: none;
    border-radius: 8px;
}
button>.links{
    width: 100%;
    color: #f2f2f2;
    text-decoration: none;
    font-size: 1.4rem;
    padding: 2px 0;
}
button>.links:hover{
    color: rgb(48, 48, 230);
    background-color: #f2f2f2;
}
button:hover{
    color: rgb(48, 48, 230);
    background-color: #f2f2f2;
    border: 1.4px solid rgb(48, 48, 230);
}