.parent-c{
    width: 90%;
    display: flex;
    overflow: hidden;
    flex-wrap: wrap;
    margin: auto;
    margin-top: 10%;
}
.parent-c .child-c{
    width: 30%;
    background: #14213d;
    color: #f3f3f3;
    margin: 10px;
    border-radius: 8px;
}
.child-c .image-c{
    width: 100%;
}
.child-c img{
    width: 100%;
    height: 17rem;
    margin-bottom: 2rem;
    border-radius: 7px 7px 0 0;
}
.child-c .info-c{
    text-align: right;
    margin-right: 6px;
}
