.container{
    width: 90%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-top: 3rem;
}
.product{
    height: 80%;
    width: 30%;
    border-radius: 8px;
    margin: 10px 10px 50px 0;
    padding-bottom: 3rem;
    display: flex;
    flex-direction: column;
    text-align: center;
}
.product:hover{
    box-shadow: 2px 2px 4px #333,
                -2px -2px 4px #333;
}
.product_img{
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 80%;
    background-color: brown;
}
.product_img img{
    width: 100%;
    height: 100%;
}
.product_info p:first-child{
    font-size: 20px;
    font-weight: 600;
}
.product_info p:nth-child(2){
    font-size: 15px;
    font-weight: 400;
    color: brown;
}
